export default {
	data() {
		return {
			Color: '#333',
			val: '',
			check1: false,
			check2: false,
			modalName: null,
			radio: '0',
			oldRadio: '0',
			select: 0,
			navList: [{
				name: '公司名称一',
				id: '0'
			}, {
				name: '公司名称二',
				id: '1'
			}, {
				name: '公司名称三',
				id: '2'
			}]
		}
	},
	methods: {
		change1() {
			this.check1 = !this.check1
		},
		change2() {
			this.check2 = !this.check2
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.radio = this.oldRadio
			this.modalName = null
		},
		RadioChange(e) {
			this.oldRadio = this.radio
			this.radio = e.detail.value
		},
		goSure() {
			this.select = this.radio
			this.modalName = null
		},
		modifyPass() {
			uni.navigateTo({
				url: '/pages/modify/modify'
			})
		}
	}
}
